.resume-container {
    padding: 2rem;
    text-align: center;
  }
  
  .download-btn {
    display: inline-block;
    margin-top: 1rem;
    padding: 0.75rem 1.5rem;
    background-color: #007bff;
    color: white;
    font-weight: bold;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.2s ease;
  }
  
  .download-btn:hover {
    background-color: #0056b3;
  }
  